"use client";

import { useLayoutEffect, useRef, useState, useEffect } from "react";
import gsap from "gsap";
import TextDecryption from "./animations/TextDecryption";
import MagneticButton from "./ui/MagneticButton";

export default function Hero() {
  const containerRef = useRef(null);
  const [counters, setCounters] = useState({
    latency: 0,
    throughput: 0,
    uptime: 0,
  });

  // Animate counters on mount
  useEffect(() => {
    const targets = { latency: 12, throughput: 2.4, uptime: 99.97 };

    const timer = setTimeout(() => {
      const interval = setInterval(() => {
        setCounters((prev) => ({
          latency: Math.min(prev.latency + 0.5, targets.latency),
          throughput: Math.min(prev.throughput + 0.1, targets.throughput),
          uptime: Math.min(prev.uptime + 4, targets.uptime),
        }));
      }, 50);

      setTimeout(() => clearInterval(interval), 2000);
    }, 1000);

    return () => clearTimeout(timer);
  }, []);

  useLayoutEffect(() => {
    const ctx = gsap.context(() => {
      // Entrance sequence
      const tl = gsap.timeline({ defaults: { ease: "power3.out" } });

      tl.from(".hero-eyebrow", {
        opacity: 0,
        y: -20,
        duration: 0.8,
      })
        .from(".hero-cta", {
          opacity: 0,
          y: 30,
          duration: 0.8,
        }, "-=0.4")
        .from(".hero-viz", {
          opacity: 0,
          x: 50,
          duration: 1,
        }, "-=0.6")
        .from(".hero-metrics", {
          opacity: 0,
          y: 20,
          duration: 0.8,
          stagger: 0.1,
        }, "-=0.4");

    }, containerRef);

    return () => ctx.revert();
  }, []);

  return (
    <section
      ref={containerRef}
      className="relative min-h-screen flex items-center justify-center overflow-hidden"
    >
      <div className="container-custom grid lg:grid-cols-[60%_40%] gap-16 items-center py-32">

        {/* LEFT: Headline + CTA */}
        <div className="space-y-12">

          {/* Eyebrow */}
          <div className="hero-eyebrow">
            <span className="inline-block px-4 py-2 border border-indigo/30 text-xs font-mono-tech text-indigo uppercase tracking-wider">
              AI-FIRST SOFTWARE COMPANY
            </span>
          </div>

          {/* Massive Headline with Text Decryption */}
          <h1 className="font-display text-[clamp(3.5rem,10vw,10rem)] leading-[0.9]">
            <TextDecryption text="PERIOXIA" className="block text-almost-white" />
            <TextDecryption text="TECHNOLOGY" className="block text-almost-white" speed={35} />
          </h1>

          {/* Subheadline */}
          <p className="text-[clamp(1.125rem,2vw,1.5rem)] text-zinc-400 leading-relaxed max-w-2xl">
            Building critical infrastructure for the AI-first enterprise.
            We don't chase trends—we architect the systems others will depend on.
          </p>

          {/* CTA */}
          <div className="hero-cta">
            <MagneticButton className="bg-indigo text-white px-10 py-5 text-lg font-bold uppercase tracking-wide glow-indigo">
              Explore Visiblo →
            </MagneticButton>
          </div>

        </div>

        {/* RIGHT: Data Flow Visualization */}
        <div className="hero-viz relative aspect-square max-w-lg mx-auto">
          <DataFlowViz />
        </div>

      </div>

      {/* BOTTOM: Live Metrics Bar */}
      <div className="absolute bottom-0 left-0 right-0 border-t border-charcoal bg-near-black/80 backdrop-blur-sm">
        <div className="container-custom">
          <div className="grid grid-cols-2 md:grid-cols-4 gap-8 py-8">

            <div className="hero-metrics text-center">
              <div className="font-mono-tech text-3xl text-cyan mb-2">
                {counters.latency.toFixed(0)}ms
              </div>
              <div className="text-xs text-zinc-600 uppercase tracking-wider">
                Latency
              </div>
            </div>

            <div className="hero-metrics text-center">
              <div className="font-mono-tech text-3xl text-violet mb-2">
                {counters.throughput.toFixed(1)}M/s
              </div>
              <div className="text-xs text-zinc-600 uppercase tracking-wider">
                Throughput
              </div>
            </div>

            <div className="hero-metrics text-center">
              <div className="font-mono-tech text-3xl text-emerald mb-2">
                {counters.uptime.toFixed(2)}%
              </div>
              <div className="text-xs text-zinc-600 uppercase tracking-wider">
                Uptime
              </div>
            </div>

            <div className="hero-metrics text-center">
              <div className="font-mono-tech text-3xl text-magenta mb-2">
                AES-256
              </div>
              <div className="text-xs text-zinc-600 uppercase tracking-wider">
                Security
              </div>
            </div>

          </div>
        </div>
      </div>

    </section>
  );
}

// Abstract Data Flow Visualization Component
function DataFlowViz() {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    // Set canvas size
    const resize = () => {
      canvas.width = canvas.offsetWidth * window.devicePixelRatio;
      canvas.height = canvas.offsetHeight * window.devicePixelRatio;
      ctx.scale(window.devicePixelRatio, window.devicePixelRatio);
    };
    resize();
    window.addEventListener("resize", resize);

    // Data nodes
    const nodes = Array.from({ length: 12 }, (_, i) => ({
      x: Math.random() * canvas.offsetWidth,
      y: Math.random() * canvas.offsetHeight,
      vx: (Math.random() - 0.5) * 0.5,
      vy: (Math.random() - 0.5) * 0.5,
      radius: Math.random() * 3 + 2,
    }));

    let animationId: number;
    const animate = () => {
      ctx.clearRect(0, 0, canvas.offsetWidth, canvas.offsetHeight);

      // Update positions
      nodes.forEach((node) => {
        node.x += node.vx;
        node.y += node.vy;

        // Bounce off edges
        if (node.x < 0 || node.x > canvas.offsetWidth) node.vx *= -1;
        if (node.y < 0 || node.y > canvas.offsetHeight) node.vy *= -1;
      });

      // Draw connections
      ctx.strokeStyle = "rgba(99, 102, 241, 0.15)";
      ctx.lineWidth = 1;
      nodes.forEach((node, i) => {
        nodes.slice(i + 1).forEach((otherNode) => {
          const dx = node.x - otherNode.x;
          const dy = node.y - otherNode.y;
          const distance = Math.sqrt(dx * dx + dy * dy);

          if (distance < 150) {
            ctx.beginPath();
            ctx.moveTo(node.x, node.y);
            ctx.lineTo(otherNode.x, otherNode.y);
            ctx.stroke();
          }
        });
      });

      // Draw nodes
      nodes.forEach((node) => {
        ctx.fillStyle = "#6366F1";
        ctx.beginPath();
        ctx.arc(node.x, node.y, node.radius, 0, Math.PI * 2);
        ctx.fill();

        // Glow
        ctx.shadowBlur = 20;
        ctx.shadowColor = "#6366F1";
        ctx.fill();
        ctx.shadowBlur = 0;
      });

      animationId = requestAnimationFrame(animate);
    };
    animate();

    return () => {
      cancelAnimationFrame(animationId);
      window.removeEventListener("resize", resize);
    };
  }, []);

  return (
    <canvas
      ref={canvasRef}
      className="w-full h-full rounded border border-indigo/20 glow-border"
    />
  );
}
